<template>
 <div class="id">
  <LuckyWheel  class="zhuan"
  ref="myLucky"
  width="600px"
  height="600px"
  :prizes="prizes"
  :blocks="blocks"
  :buttons="buttons"
  @start="startCallback"
  @end="endCallback"
/>
<imgdiv v-show="imgbox" class="imgbox" :Boximg="img" @imgdel="imgBtn"></imgdiv>
 </div>
</template>

<script>
import imgdiv from './components/imgdiv.vue'
export default {
  components:{
    imgdiv
  },
  data () {
    return {
      imgbox:false,
      img:'',
      blocks: [{ padding: '13px', background: '#617df2' }],
      prizes: [
        { fonts: [{ text: '陈平安', top: '10%' ,img:'https://wx4.sinaimg.cn/mw690/0078w6L9gy1hgsupuv0r0j30mo14g41x.jpg'}], background: '#e9e8fe' },
        { fonts: [{ text: '宁姚', top: '10%'   ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsupulu62j31401z6gv7.jpg'}], background: '#b8c5f2' },
        { fonts: [{ text: '绣虎', top: '10%'   ,img:'https://wx4.sinaimg.cn/mw690/0078w6L9gy1hgsupwjkbbj30mo14gjvn.jpg'}], background: '#e9e8fe' },
        { fonts: [{ text: '阮秀', top: '10%'   ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsuptgtx3j30mo14gn33.jpg'}], background: '#b8c5f2' },
        { fonts: [{ text: '李宝瓶', top: '10%' ,img:'https://wx3.sinaimg.cn/mw690/0078w6L9gy1hgsupt39h1j30mo14gdj0.jpg'}], background: '#e9e8fe' },
        { fonts: [{ text: '马兰花', top: '10%' ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsupwx26bj30mo14ggoa.jpg'}], background: '#b8c5f2' },
        { fonts: [{ text: '苏心斋', top: '10%' ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsupvxk06j30mo14g0wk.jpg'}], background: '#e9e8fe' },
        { fonts: [{ text: '裴钱', top: '10%'   ,img:'https://wx1.sinaimg.cn/mw690/0078w6L9gy1hgsupva481j30mo14gaf5.jpg'}], background: '#b8c5f2' },
        { fonts: [{ text: '陈念秀', top: '10%' ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsupwx26bj30mo14ggoa.jpg'}], background: '#e9e8fe' },
        { fonts: [{ text: '陈暖树', top: '10%' ,img:'https://wx2.sinaimg.cn/mw690/0078w6L9gy1hgsupw7pczj30mo14gtcu.jpg'}], background: '#b8c5f2' },
      ],
      buttons: [
        { radius: '50px', background: '#617df2' },
        { radius: '45px', background: '#afc8ff' },
        {
          radius: '40px', background: '#869cfa',
          pointer: true,
          fonts: [{ text: '开始\n抽奖', top: '-20px' }]
        },
      ],
    }
  },
  methods: {
    // 点击抽奖按钮会触发star回调
    startCallback () {
      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play()
      // 模拟调用接口异步抽奖
      setTimeout(() => {
        // 假设后端返回的中奖索引是0
        this.randomInt = Math.floor(Math.random() * ((this.prizes.length-1) - 0 + 1)) + 0;
        const index =  this.randomInt
        // 调用stop停止旋转并传递中奖索引
        this.$refs.myLucky.stop(index)
      }, 3000)
    },
    // 抽奖结束会触发end回调
    endCallback (prize) {
      this.img = prize.fonts[0].img
      setTimeout(()=>{
        this.imgbox=true
      },1000)
    },
    imgBtn(Boximg){
      this.imgbox=Boximg
    }
  }
}
</script>
<style>
  .id{
    position: relative;
  }
  .zhuan{
    margin: 0 auto;
  }
  .imgbox{
   position: absolute;
   top: 0;
   left: 400px;
  }
</style>
